/* * {
  box-sizing: content-box;
} */

.contactInfo {
  display: flex;
  justify-content: center;
}

button {
  padding: 0.56em 1.78em;
  margin: 1em;
  border-radius: 6px;
  font-weight: 500;
}
.bodyContainer {
  display: flex;
  justify-content: center;
}

.header img {
  width: 317px;
  border-radius: 10px 10px 0px 0px;
}
header {
  border-radius: 10px 10px 0px 0px;
}
.header h1 {
  font-size: 1.5625rem;
  font-weight: 700;
  line-height: 1.875rem;
  margin-bottom: 0.386rem;
}

.mainInterests {
  margin-bottom: 2rem;
}

.header h4 {
  font-weight: 400;
  font-size: 0.8rem;
  color: #f47d27;
  margin-top: 0;
  margin-bottom: 0.55rem;
}

.appContainer {
  background-color: #f5f5f5;
  color: #2b283a;
  width: 317px;
  text-align: center;
  border-radius: 10px 10px 10px 10px;
  padding: 0;
  margin-top: 44px;
  margin-bottom: 44px;
}
.btnLinkedIn {
  background-color: #297fe5;
  color: white;
  border: none;
}

.fa-envelope,
.fa-linkedin {
  margin-right: 0.6rem;
}

.btnEmail {
  border: 1px solid #d1d5db;
}
p {
  display: block;
}
.mainContainer {
  width: 247px;
  margin: 0 auto;
  text-align: left;
}

.appContainer p {
  font-size: 0.64rem;
  line-height: 15px;
}

.socialMediaLogos {
  display: flex;
  justify-content: space-between;
  padding: 1.21rem 4.56rem;
}

footer {
  background-color: #d5d4d8;
  border-radius: 0px 0px 10px 10px;
}
main h4 {
  margin-bottom: 0.3rem;
}

main p {
  margin-top: 0;
}

.mainInterests h4 {
  margin-top: 0.7rem;
}
